.sfd-coverage-value-picker-box {
    width: 652px;
    border: 2px solid #e7ecf0;
    margin: 30px 0 30px 30px;
    padding: 30px 25px;
    &.compact {
        width: 462px;
        height: 178px;
        border: 2px solid #e7ecf0;
        margin: 30px 0 30px 30px;
        padding: 20px 20px;
        .title {
            margin-bottom: 15px;
        }
    }
}

.sfd-coverage-value-picker {
    position: relative;
    padding: 0 50px;
    .row-label {
        top: 48px;
        position: absolute;
        left: 0;
        font-size: 13px;
        .item {
            display: block;
            font-weight: 400;
            line-height: 20px;
            margin-bottom: 2px;
        }
    }
    .slider-container {
        position: relative;
        overflow: hidden;
        font-size: 0;
        &:before {
            content: '';
            position: absolute;
            width: 100%;
            left: 0;
            top: 10px;
            height: 6px;
            background-color: #8b97a5;
            border-radius: 3px;
        }
    }
    .slider-list {
        display: table;
        width: 100%;
        table-layout: fixed;
    }
    .slider-item {
        display: table-cell;
        text-align: center;
        width: 124px;
        vertical-align: top;
        padding-top: 33px;
    }
    .inner-btn {
        position: relative;
        display: inline-block;
        width: 120px;
        line-height: 20px;
        color: #353942;
        text-decoration: none;
        font-weight: 400;
        padding: 13px 3px 12px;
        border: 1px solid transparent;
        vertical-align: top;
        font-size: 14px;
        border-radius: 2px;
        .label {
            color: #000;
            margin-bottom: 2px;
        }
        p {
            margin: 0;
        }
        .amount span {
            color: #fd6e0e;
        }
        &:before {
            content: '';
            position: absolute;
            left: 0;
            top: -30px;
            right: 0;
            width: 18px;
            height: 18px;
            border: 1px solid #838c93;
            margin: auto;
            border-radius: 50%;
            background-color: #fff;
            box-sizing: border-box;
        }
        &:hover:before {
            background-color: #f3f3f3;
        }
        &[aria-selected="true"] {
            font-weight: 500;
            border: 1px solid #ff7800;
            &:before {
                content: ' ';
                border: 3px solid #ff7800;
                top: -30px;
            }
            &:hover:before {
                background-color: #fff;
            }
            &:after {
                content: '';
                position: absolute;
                left: 0;
                right: 0;
                margin: auto;
                bottom: 100%;
                background-color: #fff;
                background-image: url(/ria/pc/product/common/image/sprites-coverage-value-picker.png);
                background-size: 50px 40px;
                background-position: -28px -4px;
                width: 12px;
                height: 12px;
            }
            &:focus {
                outline: none;
            }
            @media (-webkit-min-device-pixel-ratio: 2),
            (min-resolution: 192dpi) {
                &:after {
                    background-image: url(/ria/pc/product/common/image/sprites-coverage-value-picker@2x.png);
                    background-size: 50px 40px;
                    background-position: -28px -4px;
                }
            }
        }
    } // .inner-btn
    .animating .inner-btn {
        &[aria-selected="true"] {
            border-color: transparent;
            &:after {
                display: none;
            }
        }
    }
    .animation-inner-btn {
        position: relative;
        display: inline-block;
        width: 120px;
        line-height: 20px;
        color: #353942;
        font-weight: 400;
        padding: 13px 3px 12px;
        vertical-align: top;
        font-size: 14px;
        border: 1px solid #ff7800;
        border-radius: 2px;
        &:after {
            content: '';
            position: absolute;
            left: 0;
            right: 0;
            margin: auto;
            bottom: 100%;
            background-color: #fff;
            background-image: url(/ria/pc/product/common/image/sprites-coverage-value-picker.png);
            background-size: 50px 40px;
            background-position: -28px -4px;
            width: 12px;
            height: 12px;
        }
        @media (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi) {
            &:after {
                background-image: url(/ria/pc/product/common/image/sprites-coverage-value-picker@2x.png);
                background-size: 50px 40px;
                background-position: -28px -4px;
            }
        }
    } // .animation-inner-btn
    .slider-btn {
        position: absolute;
        width: 30px;
        height: 25px;
        top: 1px;
        cursor: not-allowed;
        border: 1px solid #919ca5;
        border-radius: 2px;
        opacity: 0.5;
        background-color: #fff;
        .icon {
            background-image: url(/ria/pc/product/common/image/sprites-coverage-value-picker.png);
            background-size: 50px 40px;
            color: transparent;
            font-size: 0;
            width: 7px;
            height: 13px;
        }
        @media (-webkit-min-device-pixel-ratio: 2),
        (min-resolution: 192dpi) {
            .icon {
                background-image: url(/ria/pc/product/common/image/sprites-coverage-value-picker@2x.png);
                background-size: 50px 40px;
            }
        }
        &.type-prev {
            position: absolute;
            left: 0;
            .icon {
                position: absolute;
                left: 10px;
                top: 5px;
                background-position: -4px -20px;
            }
        }
        &.type-next {
            position: absolute;
            right: 0;
            .icon {
                position: absolute;
                right: 10px;
                top: 5px;
                background-position: -16px -20px;
            }
        }
        &[aria-disabled="false"] {
            cursor: pointer;
            opacity: 1.0;
            &:hover {
                background-color: #f3f3f3;
            }
            &.type-next {
                border-color: #ff7800;
                .icon {
                    background-position: -16px -4px;
                }
            }
            &.type-prev {
                border-color: #ff7800;
                .icon {
                    background-position: -4px -4px;
                }
            }
        }
    } // .slider-btn
    &.type-compact {
        padding: 0 40px;
        .row-label {
            top: 47px;
            .item {
                letter-spacing: -1px;
                font-size: 12px;
            }
        }
        .slider-item .label {
            letter-spacing: -0.5px;
        }
        .slider-btn {
            width: 20px;
            &.type-prev .icon {
                left: 5px;
            }
            &.type-next .icon {
                right: 5px;
            }
        }
    } // .type-compact
} // .sfd-coverage-picker